import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OnboradingPrimaryComponent } from './onborading-primary/onborading-primary.component';
import { DirectorDetailsComponent } from './director-details/director-details.component';
import { ContactInformationComponent } from './contact-information/contact-information.component';
import { ProofOfIdentityComponent } from './proof-of-identity/proof-of-identity.component';
import { OnboardingCorporateMainComponent } from './onboarding-corporate-main/onboarding-corporate-main.component';
import { OtherInfoCorporateComponent } from './other-info-corporate/other-info-corporate.component';
import { CreateDirectorComponent } from './create-director/create-director.component';


export const onboardingCorporateroutes: Routes = [

  {
    path:'createDirector',
    component:CreateDirectorComponent
  },
  {
    path:'onborading-primary',
    component:OnboradingPrimaryComponent
  },
  {
    path:'Director-Details',
    component:DirectorDetailsComponent
  }, 
  {
    path:'conatctInfo',
    component:ContactInformationComponent
  },
  {
    path:'proofOfIdentity',
    component:ProofOfIdentityComponent
  },
  {
    path:'other-info',
    component:OtherInfoCorporateComponent 
  },
  {
    path:'corporate',
    component:OnboardingCorporateMainComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(onboardingCorporateroutes)],
  exports: [RouterModule]
})
export class OnboardingCorporateRoutingModule { }
